import React, { useRef, useState } from 'react';
import { Button, Container, Form, Row, Col, Modal, InputGroup, FormControl } from 'react-bootstrap';
import CopyToClipboard from "react-copy-to-clipboard";
import { Cookies } from 'react-cookie';

import iconRefresh from './assets/icons/arrow-clockwise.svg';
import iconCopy from './assets/icons/clipboard.svg';
import iconSettings from './assets/icons/gear.svg';

import './App.css';

const cookie = new Cookies();

const upper = 'ABCDEFGHIJKLMNOPQRSTUVWXYZ';
const lower = 'abcdefghijklmnopqrstuvwxyz';
const num = '1234567890';
const special = '~`!@#$%^&*()_-+={[}]|:;<,>.?/';

const initConfig = {
  upper: true,
  lower: true,
  num: true,
  special: true,
  ignore: false,
  chars: '',
  size: 20
};

const config = Object.assign(JSON.parse(JSON.stringify(initConfig)), cookie.get('config'));

let container = '';

applyConfig();

function applyConfig() {
  container = '';

  if(config.upper) {
    container += upper;
  }

  if(config.lower) {
    container += lower;
  }

  if(config.num) {
    container += num;
  }

  if(config.special) {
    container += special;
  }

  if(config.ignore) {
    // container = container.replace(new RegExp('[' + config.chars + ']', 'g'), '');

    for(const char of config.chars) {
      container = container.replace(char, '');
    }
  }
}

function generate() {
  let str = '';

  if(!container) {
    return 'Check settings.';
  }

  while(str.length < config.size) {
    str += container.charAt(Math.round(Math.random() * container.length));
  }

  return str;
}

function App() {
  const [result, setResult] = useState(generate());
  const [copied, setCopied] = useState(false);
  const [modalShow, setModalShow] = useState(false);

  function onRefresh() {
    setCopied(false);
    setResult(generate());
  }

  function onCopy() {
    setCopied(true);
  }

  return (
    <>
    <div className="wrapper">
      <Container>
        <Row>
          <Col>
            <Form.Control size="lg" className="text-center" type="text" value={result} readOnly />
          </Col>
        </Row>

        <Row className="mt-2">
          <Col>
            <Button size="lg" variant="secondary" className="w-100" onClick={onRefresh}>
              <img src={iconRefresh} alt="Refresh" />
            </Button>
          </Col>
          <Col>
            <CopyToClipboard text={result} onCopy={onCopy}>
              <Button size="lg" variant={copied ? "success" : "secondary"} className="w-100">
                <img src={iconCopy} alt="Copy" />
              </Button>
            </CopyToClipboard>
          </Col>
        </Row>
      </Container>
    </div>

    <div className='fixed-top text-end p-2'>
      <Button variant="secondary" onClick={() => setModalShow(true)}>
        <img src={iconSettings} alt="Settings" />
      </Button>
    </div>

    <SettingModal
      show={modalShow}
      onHide={() => setModalShow(false)}
    />
    </>
  );
}

function SettingModal(props) {
  const [size, setSize] = useState(config.size);
  const [validated, setValidated] = useState(false);
  
  const upperRef = useRef(null);
  const lowerRef = useRef(null);
  const numRef = useRef(null);
  const specialRef = useRef(null);
  const ignoreRef = useRef(null);
  const charsRef = useRef(null);
  const sizeRef = useRef(null);

  function onDefault() {
    setValue(initConfig);
  }

  function onReset() {
    setValue(config);
  }

  function setValue(value) {
    upperRef.current.checked = value.upper;
    lowerRef.current.checked = value.lower;
    numRef.current.checked = value.num;
    specialRef.current.checked = value.special;
    ignoreRef.current.checked = value.ignore;
    charsRef.current.value = value.chars;
    sizeRef.current.value = value.size;

    setSize(value.size);
  }

  function onSave() {
    config.upper = upperRef.current.checked;
    config.lower = lowerRef.current.checked;
    config.num = numRef.current.checked;
    config.special = specialRef.current.checked;
    config.ignore = ignoreRef.current.checked;
    config.chars = charsRef.current.value;
    config.size = sizeRef.current.value;

    if(!config.upper && !config.lower && !config.num && !config.special) {
      setValidated(true);
    }
    else {
      cookie.set('config', config);

      applyConfig();

      props.onHide();
    }
  }

  function onChange(item) {
    setSize(item.currentTarget.value);
  }

  return (
    <Modal {...props} centered>
      <Modal.Header closeButton>
        <Modal.Title>Settings</Modal.Title>
      </Modal.Header>

      <Modal.Body>
        <Form.Check ref={upperRef} id={"upper"} type={"checkbox"} label={upper} defaultChecked={config.upper} required />
        <Form.Check ref={lowerRef} id={"lower"} type={"checkbox"} label={lower} defaultChecked={config.lower} required />
        <Form.Check ref={numRef} id={"num"} type={"checkbox"} label={num} defaultChecked={config.num} required />
        <Form.Check ref={specialRef} id={"special"} type={"checkbox"} label={special} defaultChecked={config.special} required />
        { validated && <Form.Text className='text-danger'>At least one must be selected.</Form.Text>}
        <hr />
        <Form.Check ref={ignoreRef} id={"ignore"} type={"checkbox"} label={"Ignore Characters"} defaultChecked={config.ignore} />
        <FormControl ref={charsRef} id={"chars"} maxLength={90} defaultValue={config.chars} />
        <hr />
        <Form.Label>Size {size}</Form.Label>
        <Form.Range ref={sizeRef} min={4} max={30} defaultValue={config.size} onChange={onChange} />
      </Modal.Body>

      <Modal.Footer>
      <Button size="sm" variant="secondary" onClick={onDefault}>Default</Button>
      <Button size="sm" variant="secondary" onClick={onReset}>Reset</Button>
        <Button size="sm" variant="secondary" onClick={props.onHide}>Close</Button>
        <Button size="sm" variant="primary" onClick={onSave}>Save</Button>
      </Modal.Footer>
    </Modal>
  );
}

export default App;
